.header {
  height: 80px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 30px;
  border-bottom: 1px solid #ececec;
}

.logo {
  margin-right: 40px;
  margin-bottom: 7px;
}

.main-menu {
  margin-right: 70px;
  list-style: none;
  display: flex;
}

.main-menu__link {
  color: #222222;
  font-size: 14px;
  display: block;
  padding: 0 20px;
  line-height: 80px;
}

.main-menu__link [class*="icon"] {
  font-size: 6px;
  color: #1f4ba5;
  padding: 0 0 0 10px;
}

.dropdown {
  position: relative;
}

.dropdown:hover .dropdown-content {
  opacity: 1;
  margin-top: 0;
  left: -10px;
  visibility: visible;
}

.dropdown--right:hover .dropdown-content {
  left: auto;
  right: -10px;
}

.dropdown--right .dropdown-content:before {
  right: 30px;
  z-index: 1;
}

.dropdown--right .dropdown-content:after {
  right: 29px;
}

.dropdown--white .dropdown-content {
  background-color: #fff;
  border: 1px solid #ececec;
  padding: 0;
}

.dropdown--white .dropdown-content:before {
  border-bottom-color: #fff;
}

.dropdown--white .dropdown-content:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 100%;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 8px 9px 8px;
  border-color: transparent transparent #ececec transparent;
}

.dropdown-content {
  position: absolute;
  width: 240px;
  background-color: #222222;
  padding: 30px;
  z-index: 99;
  top: 100%;
  left: -10000px;
  visibility: hidden;
  opacity: 0;
  margin-top: 30px;
  transition: opacity 0.3s, margin-top 0.5s;
}

.dropdown-content:before {
  content: '';
  display: block;
  position: absolute;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 7px 7px 7px;
  border-color: transparent transparent #222222 transparent;
  bottom: 100%;
}

.dropdown-menu {
  list-style: none;
}

.dropdown-menu__item {
  margin-bottom: 8px;
}

.dropdown-menu__link {
  font-size: 12px;
  color: rgba(188, 188, 188, 0.7);
  line-height: 16px;
}

.dropdown-menu__link:hover {
  color: #ffffff;
}

.search {
  position: relative;
  flex: 1;
  margin-right: 50px;
}

.search__input {
  width: 100%;
  height: 30px;
  font-size: 12px;
  font-family: "PTSerif";
  color: #222222;
  background-color: #f6f6f6;
  padding: 0 40px 0 20px;
  border: none;
  border-radius: 20px;
}

.search__input:-ms-input-placeholder {
  color: #868686;
  font-style: italic;
}

.search__input:-moz-placeholder {
  color: #868686;
  font-style: italic;
}

.search__input::-moz-placeholder {
  color: #868686;
  font-style: italic;
}

.search__input::-webkit-input-placeholder {
  color: #868686;
  font-style: italic;
}

.search__input::placeholder {
  color: #868686;
  font-style: italic;
}

.search__button {
  position: absolute;
  top: 7px;
  right: 12px;
  font-family: 'icomoon' !important;
  background-color: transparent;
  border: none;
}

.location {
  margin-right: 50px;
}

.location__link {
  color: #868686;
  font-size: 12px;
}

.location [class*="icon"] {
  font-size: 18px;
  margin-right: 5px;
  color: #222222;
}

.profile-menu {
  list-style: none;
  display: flex;
  margin-right: -20px;
}

.profile-menu__item {
  position: relative;
}

.profile-menu__link {
  display: block;
  line-height: 80px;
  padding: 0 20px;
}

.profile-menu [class*="icon"] {
  font-size: 18px;
  color: #222222;
  vertical-align: middle;
}

.profile-menu .dropdown-content {
  width: auto;
}

.basket {
  position: relative;
  line-height: 1.4;
}

.basket .badge {
  position: absolute;
  top: -7px;
  right: -11px;
}

.b-price {
  margin-left: 15px;
  font-size: 12px;
  font-family: Oswald;
  color: #222222;
  font-weight: 700;
  line-height: 1.2;
  vertical-align: middle;
  letter-spacing: 1.2px;
}

.badge {
  min-width: 16px;
  height: 16px;
  background-color: #1f4ba5;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  line-height: 17px;
  color: #ffffff;
  font-size: 9px;
  padding: 0 4px;
}

.user-menu {
  width: 180px;
  list-style: none;
}

.user-menu__item {
  border-bottom: 1px solid #ececec;
}

.user-menu__item:last-child {
  border: none;
}

.user-menu__link {
  display: flex;
  align-items: center;
  color: #222222;
  font-size: 12px;
  font-weight: 400;
  line-height: 40px;
  padding: 0 20px;
}

.user-menu [class*="icon"] {
  width: 18px;
  font-size: 16px;
  color: #ccc;
  margin-right: 15px;
}

.user-menu .badge {
  margin-left: auto;
}

.cart {
  width: 300px;
}

.cart-list {
  list-style: none;
}

.cart-list__item {
  display: flex;
  align-items: center;
  border-bottom: 1px solid #ececec;
  padding: 10px;
}

.cart-list__img {
  flex: none;
  width: 60px;
  text-align: center;
  margin-right: 20px;
}

.cart-list__img img {
  display: inline-block;
  max-width: 100%;
}

.product__title {
  color: #222222;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}
